:root {
    --main-bg-color: #FAFAFA;
    --main-text-color: #212121;
    --main-link-color: #0D47A1;
    --hovered-link-color: #1565C0;
    --secondary-bg-color: #BBDEFB;
    --tertiary-bg-color: #E3F2FD;
}

html, body {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
}

body {
    background-color: var(--main-bg-color);
    color: var(--main-text-color);
    font-family: 'Roboto', sans-serif;
    display: flex;
    width: 100%;
    max-width: 740px;
    flex-direction: column;
}

header {
    padding: 18px;
}

main {
    margin: 12px;
}

h1, h2, h3 {
    margin: 0;
    padding: 0;
}

h3 {
    margin-bottom: 6px;
}

button {
    margin: 0 12px;
}

.site-name {
    text-align: center;
}

.site-name a:link, .site-name a:hover, .site-name a:visited, .site-name a:active {
    color: var(--main-text-color);
}

.site-name a, .menu__horizontal a {
    text-decoration: none;
}

.form-container {
    display: flex;
    flex-direction: column;
}

.alert-container {
    margin: 12px 0;
}

ul {
    margin: 0;
    padding: 0;
}

.menu__horizontal {
    padding: 12px;
}

.menu__horizontal li {
    display: inline;
    list-style-type: none;
    margin: 0 6px;
}

.topbar {
    padding-top: 24px;
    display: flex;
    justify-content: flex-end;
    background-color: var(--secondary-bg-color);
    border-radius: 0 0 8px 8px;
    height: 19px;
}

.main-navigation {
    background-color: var(--tertiary-bg-color);
    border-radius: 8px;
    display: flex;
    justify-content: center;
    font-size: large;
}

.product-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 16px 0;
}

.card__product-preview {
    margin: 8px 0;
    width: 100%;
}

.card__product-preview:first-of-type {
    margin-top: 0;
}

.MuiFormControl-root {
    margin: 8px 0;
}

/* Links */
/* unvisited link */
a:link {
    color: var(--main-link-color);
}

/* visited link */
a:visited {
    color: var(--main-link-color);
}

/* mouse over link */
a:hover {
    color: var(--hovered-link-color);
}

/* selected link */
a:active {
    color: var(--main-link-color);
}

@media screen and (max-width: 300px) {
    .topbar__display-name {
        display: inline-block;
        max-width: 120px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}
